<template>
  <v-row justify="center" align="center">
      <v-form
        ref="form"
        v-model="valid"
        style="min-width:500px"
      >
        <v-text-field
          v-model="id"
          :rules="idRules"
          :counter="20"
          label=""
          @keyup="valCheck(1)"
          required
        ></v-text-field>

        <v-text-field
          v-model="pass"
          :rules="passRules"
          type="password"
          label=""
          required
        ></v-text-field>

        <v-text-field
          v-model="passConf"
          :rules="passConfRules"
          type="password"
          label=" "
          required
        ></v-text-field>

        <v-text-field
          v-model="email"
          :rules="emailRules"
          label="E-mail"
          @keyup="valCheck(2)"
          required
        ></v-text-field>
        <v-btn
          :disabled="!valid"
          color="success"
          class="mr-4"
          @click="register()"
        >
          
        </v-btn>
      </v-form>
    </v-card>
  </v-row>
</template>

<script>
import axios from 'axios'
import sha256 from 'js-sha256'
  export default {
    data: () => ({
      valid: true,
      id: '',
      pass: '',
      passRules: [
        v => !!v || ' ',
        v => (/^(?=.*[a-zA-Z])(?=.*\d).{8,20}$/.test(v) && v.length >= 8 && v.length <= 20) || ' 8~20     .',
      ],
      passConf: '',
      email: '@inu.ac.kr',
      idCheck: false,
      emailCheck: false,
    }),
    methods: {
      register () {
          axios.post('/regi-done', {
            id : this.id,
            pass : sha256(this.pass),
            email : this.email,
          }).then(res => {
            alert(" .")
            this.$router.push({path:'/login'})
          }).catch(err => {
            alert('   .')
          })
      },
      valCheck(req) {
        var query=""
        if (req == 1) query = `id=${this.id}`
        else if (req == 2) query = `email=${this.email}`
        axios.get(`/register/valid?` + query)
        .then(res => {
          if (req == 1) this.idCheck = res.data.status
          else if (req == 2) this.emailCheck = res.data.status
        })
      },
    },
    computed: {
      idRules() {
        const rules=[v => !!v || 'ID ',]
        rules.push(v => (/^[a-zA-Z0-9]+/.test(v) && v.length >= 2 && v.length <= 20) || ' 2~20   .',)
        rules.push(this.idCheck || ' ID .')
        return rules
      },
      emailRules() {
        const rules=[v => !!v || ' ',]
        rules.push(v => /^[a-zA-Z0-9]+@inu\.ac\.kr/.test(v) || ' @inu.ac.kr  .')
        rules.push(this.emailCheck || '  .')
        return rules
      },
      passConfRules() {
        const rules=[v => !!v || ' ',]
        rules.push(this.pass === this.passConf || ' ')
        return rules
      },
    },
  }
</script>
